#header{
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 500px;
    background-image: url("../../public/imagens/Background_header.png");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: top;
    font-weight: 700;
    color: #E1FFE1;
}

.container{
    width: 1216px;
    padding: 40px 112px 0px 112px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

ul{
    display: flex;
    align-items: center;
    column-gap: 40px;
}